import { Button } from "@ya.praktikum/react-developer-burger-ui-components";
import { FC, useState, ChangeEvent, FormEvent } from "react";
import { Route, Routes, NavLink } from "react-router-dom";
// Actions
import { updateUserInfo, logOut } from "../../services/user/actions";
// Components
import Form from "../../components/form/form";
import ProfileOrderFeedPage from "../profileOrderFeedPage/profile-order-feed-page";
// Hooks
import { useAppDispatch, useAppSelector } from "../../services/hooks";
// Styles
import styles from "./profile-page.module.css";

const ProfilePage: FC = () => {
  const dispatch = useAppDispatch();
  const { user } = useAppSelector((store) => store?.user);
  const [isShowButtons, setIsShowButtons] = useState<boolean>(false);
  const [userInfo, setUserInfo] = useState({
    name: user.name,
    email: user.email,
    password: "",
  });
  const cancelChanges = () =>
    setUserInfo({ name: user.name, email: user.email, password: "" });
  const saveChanges = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    updateUserInfo(userInfo)(dispatch);
  };
  const logOutUser = () => {
    logOut()(dispatch);
  };
  const pageStructure = {
    form: [
      {
        placeholder: "",
        type: "text",
        value: userInfo.name,
        icon: true,
        onChange: (e: ChangeEvent<HTMLInputElement>) => {
          setUserInfo({ ...userInfo, name: e.target.value });
          setIsShowButtons(true);
        },
      },
      {
        placeholder: "E-mail",
        type: "email",
        value: userInfo.email,
        icon: true,
        onChange: (e: ChangeEvent<HTMLInputElement>) => {
          setUserInfo({ ...userInfo, email: e.target.value });
          setIsShowButtons(true);
        },
      },
      {
        placeholder: "",
        type: "password",
        value: userInfo.password,
        icon: true,
        onChange: (e: ChangeEvent<HTMLInputElement>) => {
          setUserInfo({ ...userInfo, password: e.target.value });
          setIsShowButtons(true);
        },
      },
    ],
    buttons: [
      {
        title: "",
        type: "secondary",
        onClick: cancelChanges,
      },
      {
        title: "",
        type: "primary",
        htmlTypeSubmit: true,
      },
    ],
    showButtons: isShowButtons,
    onsubmit: saveChanges,
  };
  return (
    <main className={styles.main}>
      <aside className={styles.aside}>
        <section className={styles.navigation}>
          <NavLink
            className={({ isActive }) =>
              isActive ? styles.active : styles.link
            }
            to="/profile"
            end
          >
            
          </NavLink>
          <NavLink
            className={({ isActive }) =>
              isActive ? styles.active : styles.link
            }
            to="/profile/orders"
            end
          >
             
          </NavLink>
          <Button
            className={styles.link}
            htmlType="button"
            type="secondary"
            onClick={logOutUser}
          >
            
          </Button>
        </section>
        <p className={styles.note}>
                  
        </p>
      </aside>
      <div className={styles.fragment}>
        <Routes>
          <Route path="/" element={<Form structure={pageStructure} />} />
          <Route path="orders" element={<ProfileOrderFeedPage />} />
        </Routes>
      </div>
    </main>
  );
};

export default ProfilePage;
