import { ChangeEvent, FC, useState, FormEvent } from "react";
import { useNavigate } from "react-router-dom";
// Components
import Form from "../../components/form/form";
// Utils
import { postForgotPasswordApi } from "../../utils/burger-api";
// Hooks

const ForgotPasswordPage: FC = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const restorePassword = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    postForgotPasswordApi({ email: email }).then(
      (res) => res.success && navigate("/reset-password")
    );
  };
  const pageStructure = {
    title: " ",
    form: [
      {
        placeholder: "E-mail",
        type: "email",
        value: email,
        onChange: (e: ChangeEvent<HTMLInputElement>) => {
          setEmail(e.target.value);
        },
      },
    ],
    buttons: [
      {
        title: "",
        type: "primary",
        htmlTypeSubmit: true,
      },
    ],
    notifications: [
      { title: " ?", link: { title: "", url: "/login" } },
    ],
    showButtons: true,
    onsubmit: restorePassword,
  };
  return <Form structure={pageStructure} />;
};

export default ForgotPasswordPage;
